@use '../theme' as *;
@use '../hover' as *;
@use '../settings' as *;
@use '../settings/mixins.scss' as *;

@mixin c-link(
	$color: theme-color('color'),
	$color-hover: theme-color('hover', 'color'),
	$text-decoration: get-existing-value($link-decoration, none),
	$text-decoration-hover: get-existing-value($link-hover-decoration, none),
	$font-size: get-existing-value($link-font-size, $font-size-base),
	$font-weight: get-existing-value($link-font-weight, $font-weight-base),
	$line-height: get-existing-value($link-line-height, $line-height-base)
) {
	color: $color;
	text-decoration: $text-decoration;
	font-size: $font-size;
	font-weight: $font-weight;
	line-height: $line-height;

	@include m-hover-focus-active {
		color: $color-hover;
		text-decoration: $text-decoration-hover;
	}
}
