@use 'sass:map';
@use '../../variables' as *;

.h1__container {
	width: max-content;
	padding: map.get($spacers, 3) map.get($spacers, 6) map.get($spacers, 3) 0;
	background-color: $color-primary;
}

h2,
.h2 {
	color: $color-primary;
}

h3,
.h3 {
	font-weight: map.get($font-weight, light);
	line-height: map.get($line-height, heading-small);
}

h4,
.h4 {
	line-height: map.get($line-height, heading-small);
}

@include themify() {
	h1,
	.h1,
	h3,
	.h3 {
		color: theme-color(headings-light);
	}
}
