@use '../theme' as *;
@use '../settings' as *;

@mixin c-button(
	$font-size: 1rem,
	$spacing-vertical: 0.5em,
	$spacing-horizontal: 1em,
	$line-height: 1,
	$border-width: 1px,
	$border-radius: 0
) {
	@include c-button-size(
		$font-size,
		$spacing-vertical,
		$spacing-horizontal,
		$line-height,
		$border-width,
		$border-radius
	);

	display: inline-block;
	margin: 0;
	vertical-align: top;
	white-space: nowrap;
	font-family: inherit;
	line-height: 1;
	border: 0;
	cursor: pointer;

	&,
	&:hover,
	&:active,
	&.is-active {
		outline: none;
		text-decoration: none;
	}
}

@mixin c-button-theme(
	$background-color: theme-color('background'),
	$background-color-active: theme-color('background-active', 'background'),
	$color: theme-color('color'),
	$color-active: theme-color('color-active', 'color'),
	$border-color: theme-color('border', 'background'),
	$border-color-active:
		theme-color('border-active', 'border', 'background-active', 'background')
) {
	transition: all $default-transition-duration $default-transition-function;
	background-color: $background-color;
	color: $color;
	border-color: $border-color;

	&:hover,
	&:active,
	&.is-active {
		background-color: $background-color-active;
		color: $color-active;
		border-color: $border-color-active;
	}
}

@mixin c-button-size(
	$font-size: 1rem,
	$spacing-vertical: 0.5em,
	$spacing-horizontal: 1em,
	$line-height: 1,
	$border-width: 1px,
	$border-radius: 0
) {
	font-size: $font-size;
	line-height: $line-height;
	border-width: $border-width;
	padding: $spacing-vertical $spacing-horizontal;
	border-radius: $border-radius;
}

@mixin c-button-full-width() {
	width: 100%;
	text-align: center;
}
