@use '../grid' as *;
@use '../breakpoints' as *;
@use '../settings' as *;
@use '../theme' as *;

@mixin u-white-space() {
	.text-nowrap {
		white-space: nowrap !important;
	}
}

@mixin u-font-style() {
	.font-italic {
		font-style: italic !important;
	}

	.font-normal {
		font-style: normal !important;
	}

	.font-oblique {
		font-style: oblique !important;
	}
}

@mixin m-truncate() {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

@mixin u-text-truncate() {
	.text-truncate {
		@include m-truncate;
	}
}

@mixin u-text-transform() {
	.text-lowercase {
		text-transform: lowercase !important;
	}

	.text-uppercase {
		text-transform: uppercase !important;
	}

	.text-capitalize {
		text-transform: capitalize !important;
	}
}

@mixin u-text-align($grid-breakpoints: $grid-breakpoints) {
	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			$infix: breakpoint-infix($breakpoint, $grid-breakpoints);

			.text#{$infix}-justify {
				text-align: justify !important;
			}

			.text#{$infix}-left {
				text-align: left !important;
			}
			.text#{$infix}-right {
				text-align: right !important;
			}
			.text#{$infix}-center {
				text-align: center !important;
			}
		}
	}
}

@mixin u-font-size($font-size-mapping: $font-size) {
	@each $size, $value in $font-size-mapping {
		.font-size-#{$size} {
			font-size: $value !important;
		}
	}
}

@mixin u-font-size-responsive(
	$font-size-mapping: $font-size,
	$grid-breakpoints: $grid-breakpoints
) {
	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			$infix: breakpoint-infix($breakpoint, $grid-breakpoints);
			@each $size, $value in $font-size-mapping {
				.font-size#{$infix}-#{$size} {
					font-size: $value !important;
				}
			}
		}
	}
}

@mixin u-font-weight($font-weight-mapping: $font-weight) {
	@each $weight, $value in $font-weight-mapping {
		.font-weight-#{'' + $weight} {
			font-weight: $value !important;
		}
	}
}

@mixin u-font-weight-responsive(
	$font-weight-mapping: $font-weight,
	$grid-breakpoints: $grid-breakpoints
) {
	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			$infix: breakpoint-infix($breakpoint, $grid-breakpoints);
			@each $weight, $value in $font-weight-mapping {
				.font-weight#{$infix}-#{$weight} {
					font-weight: $value !important;
				}
			}
		}
	}
}

@mixin u-text-color($themes: $themes) {
	.u-color {
		@include themify($style-base: true, $themes: $themes) {
			color: theme-color('text');
		}
	}
}
