@use 'sass:map';
@use '../hover' as *;
@use '../settings' as *;

@mixin u-reboot-theme(
	$font-family-base: $font-family-base,
	$font-size: map.get($font-size, 'base'),
	$font-weight-base: map.get($font-weight, 'base'),
	$line-height-base: map.get($line-height, 'base'),
	$body-color: theme-color('text'),
	$body-bg: theme-color('background'),
	$link-color: theme-color('link', 'default'),
	$link-hover-color: theme-color('link-hover', 'default'),
	$link-decoration: none,
	$link-hover-decoration: $link-decoration,
	$hr-opacity: 1,
	$hr-color: theme-color('divider', 'border', 'text')
) {
	body {
		font-family: $font-family-base;
		font-size: $font-size;
		font-weight: $font-weight-base;
		line-height: $line-height-base;
		color: $body-color;
		background-color: $body-bg;
	}

	a {
		color: $link-color;
		text-decoration: $link-decoration;

		&:hover {
			color: $link-hover-color;
			text-decoration: $link-hover-decoration;
		}
	}

	hr {
		border-top-color: $hr-color;
		opacity: $hr-opacity;
	}
}

@mixin u-reboot(
	$table-cell-padding: 0.25rem,
	$paragraph-margin-bottom: 0.5rem,
	$headings-margin-bottom: $paragraph-margin-bottom,
	$label-margin-bottom: $paragraph-margin-bottom,
	$font-size-root: null
) {
	*,
	*::before,
	*::after {
		box-sizing: border-box;
	}

	:root {
		@if $font-size-root != null {
			@include font-size(var(--#{$prefix}root-font-size));
		}

		@media (prefers-reduced-motion: no-preference) {
			scroll-behavior: smooth;
		}
	}

	html {
		text-size-adjust: 100%;
		min-height: 100%;
		-ms-overflow-style: scrollbar;
		-webkit-tap-highlight-color: rgba(0 0 0 / 0%);
	}

	body {
		margin: 0;
		text-align: left;
		text-size-adjust: 100%;
		-webkit-tap-highlight-color: rgba(0 0 0 / 0%);
		min-height: 100%;
	}

	hr {
		margin: 1rem 0;
		color: inherit;
		border: 0;
		border-top: 1px solid currentColor;
		border-bottom-width: 0;
		opacity: 1;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-top: 0;
		margin-bottom: $headings-margin-bottom;
		font-family: inherit;
		font-weight: inherit;
		line-height: inherit;
		font-size: inherit;
	}

	p {
		margin-top: 0;
		margin-bottom: $paragraph-margin-bottom;
	}

	abbr[title] {
		text-decoration: underline dotted;
		cursor: help;
		text-decoration-skip-ink: none;
	}

	address {
		margin-bottom: 1rem;
		font-style: normal;
		line-height: inherit;
	}

	ol,
	ul {
		padding-left: 2rem;
	}

	ol,
	ul,
	dl {
		margin-top: 0;
		margin-bottom: 1rem;
	}

	ol ol,
	ul ul,
	ol ul,
	ul ol {
		margin-bottom: 0;
	}

	dt {
		font-weight: bold;
	}

	dd {
		margin-bottom: 0.5rem;
		margin-left: 0;
	}

	blockquote {
		margin: 0 0 1rem;
	}

	b,
	strong {
		font-weight: bolder;
	}

	small {
		font-size: 80%;
	}

	mark {
		padding: 0;
		background-color: #ffff00;
	}

	sub,
	sup {
		position: relative;
		font-size: 80%;
		line-height: 0;
		vertical-align: baseline;
	}

	sub {
		bottom: -0.25em;
	}
	sup {
		top: -0.5em;
	}

	a:not([href]):not([class]) {
		&,
		&:hover {
			color: inherit;
			text-decoration: none;
		}
	}

	pre,
	code,
	kbd,
	samp {
		font-family: inherit;
		font-size: inherit;
	}

	pre {
		display: block;
		margin-top: 0;
		margin-bottom: 1rem;
		overflow: auto;
		font-size: inherit;
		color: inherit;

		code {
			font-size: inherit;
			color: inherit;
			word-break: normal;
		}
	}

	code {
		font-size: inherit;
		color: inherit;
		word-wrap: break-word;

		a > & {
			color: inherit;
		}
	}

	kbd {
		padding: 0;
		font-size: inherit;
		color: inherit;
		background-color: transparent;
		border-radius: 0;

		kbd {
			padding: 0;
			font-size: 1em;
			font-weight: inerhit;
		}
	}

	figure {
		margin: 0 0 1rem;
	}

	img,
	svg {
		vertical-align: middle;
	}

	table {
		caption-side: bottom;
		border-collapse: collapse;
	}

	caption {
		padding-top: $table-cell-padding;
		padding-bottom: $table-cell-padding;
		text-align: left;
	}

	th {
		font-weight: bold;
		text-align: inherit;
		text-align: -webkit-match-parent;
	}

	thead,
	tbody,
	tfoot,
	tr,
	td,
	th {
		border-color: inherit;
		border-style: solid;
		border-width: 0;
	}

	label {
		display: inline-block;
		margin-top: 0;
		margin-bottom: $label-margin-bottom;
	}

	button {
		border-radius: 0;
	}

	button:focus:not(:focus-visible) {
		outline: 0;
	}

	input,
	button,
	select,
	optgroup,
	textarea {
		margin: 0;
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
	}

	button,
	select {
		text-transform: none;
	}

	[role='button'] {
		cursor: pointer;
	}

	select {
		word-wrap: normal;

		&:disabled {
			opacity: 1;
		}
	}

	[list]:not([type='date']):not([type='datetime-local']):not([type='month']):not([type='week']):not([type='time'])::-webkit-calendar-picker-indicator {
		display: none !important;
	}

	button,
	[type='button'],
	[type='reset'],
	[type='submit'] {
		-webkit-appearance: button;

		&:not(:disabled) {
			cursor: pointer;
		}
	}

	::-moz-focus-inner {
		padding: 0;
		border-style: none;
	}

	textarea {
		resize: vertical;
	}

	fieldset {
		min-width: 0;
		padding: 0;
		margin: 0;
		border: 0;
	}

	legend {
		float: left;
		width: 100%;
		padding: 0;
		margin-bottom: 1rem;
		font-size: inherit;
		font-weight: bold;
		line-height: inherit;

		+ * {
			clear: left; // 2
		}
	}

	::-webkit-datetime-edit-fields-wrapper,
	::-webkit-datetime-edit-text,
	::-webkit-datetime-edit-minute,
	::-webkit-datetime-edit-hour-field,
	::-webkit-datetime-edit-day-field,
	::-webkit-datetime-edit-month-field,
	::-webkit-datetime-edit-year-field {
		padding: 0;
	}

	::-webkit-inner-spin-button {
		height: auto;
	}

	[type='search'] {
		outline-offset: -2px;
		-webkit-appearance: textfield;
	}

	::-webkit-search-decoration {
		-webkit-appearance: none;
	}

	::-webkit-color-swatch-wrapper {
		padding: 0;
	}

	::file-selector-button {
		font: inherit;
		-webkit-appearance: button;
	}

	output {
		display: inline-block;
	}

	iframe {
		border: 0;
	}

	summary {
		display: list-item;
		cursor: pointer;
	}

	progress {
		vertical-align: baseline;
	}

	[hidden] {
		display: none !important;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
