@use '../settings' as *;

@mixin m-hover {
	&:hover {
		@content;
	}
}

@mixin m-hover-focus {
	&:focus,
	&:hover {
		@content;
	}
}

@mixin m-plain-hover-focus {
	&,
	&:focus,
	&:hover {
		@content;
	}
}

@mixin m-hover-focus-active {
	&:focus,
	&:active,
	&:hover {
		@content;
	}
}

@mixin m-hover-focus-active-class($active-class: $active-class) {
	&:focus,
	&:active,
	&:hover,
	&.#{$active-class} {
		@content;
	}
}
