@use '../breakpoints' as *;
@use '../settings' as *;

@mixin u-fade(
	$fade-show-class: fade--show,
	$transition-fade: opacity 0.15s linear
) {
	opacity: 0;
	visibility: hidden;
	transition: $transition-fade;

	&.#{$fade-show-class} {
		opacity: 1;
		visibility: visible;
	}
}

@mixin u-collapse($collapse-show-class: collapse--show) {
	display: none;

	&.#{$collapse-show-class} {
		display: block;
	}
}

@mixin u-collapse-table(
	$collapse-class: collapse,
	$collapse-show-class: collapse--show
) {
	tr {
		&.#{$collapse-class}.#{$collapse-show-class} {
			display: table-row;
		}
	}

	tbody {
		&.#{$collapse-class}.#{$collapse-show-class} {
			display: table-row-group;
		}
	}
}

@mixin u-collapsing($transition-collapse: height 0.35s ease) {
	position: relative;
	height: 0;
	overflow: hidden;
	transition: $transition-collapse;
}

@mixin u-fading($transition-collapse: height 0.35s ease) {
	visibility: visible;
	transition: $transition-collapse;
}

@mixin u-collapse-responsive($breakpoints: $grid-breakpoints) {
	@each $breakpoint in map-keys($breakpoints) {
		$infix: breakpoint-infix($breakpoint, $breakpoints);

		@include media-breakpoint-up($breakpoint, $breakpoints) {
			.collapse-open#{$infix} {
				display: block !important;
				height: auto !important;
			}
		}
	}
}
