@use 'sass:map';
@use '../settings' as *;
@use '../breakpoints' as *;

@mixin s-headings-mobile($headings-mobile: $headings-mobile) {
	@each $heading, $fontsize in $headings-mobile {
		#{$heading},
		.#{$heading} {
			font-size: $fontsize;
		}
	}
}

@mixin s-headings-mobile-class-only($headings-mobile: $headings-mobile) {
	@each $heading, $fontsize in $headings-mobile {
		.#{$heading} {
			font-size: $fontsize;
		}
	}
}

@mixin s-headings(
	$headings: $headings,
	$headings-desktop-breakpoint: $headings-desktop-breakpoint
) {
	@include media-breakpoint-up($headings-desktop-breakpoint) {
		@each $heading, $fontsize in $headings {
			#{$heading},
			.#{$heading} {
				font-size: $fontsize;
			}
		}
	}
}

@mixin s-headings-class-only(
	$headings: $headings,
	$headings-desktop-breakpoint: $headings-desktop-breakpoint
) {
	@include media-breakpoint-up($headings-desktop-breakpoint) {
		@each $heading, $fontsize in $headings {
			.#{$heading} {
				font-size: $fontsize;
			}
		}
	}
}

@mixin s-headings-styles(
	$headings-font-family: $headings-font-family,
	$headings-font-weight: $headings-font-weight,
	$headings-line-height: map.get($line-height, 'heading'),
	$headings-color: theme-color('headings', 'text'),
	$headings-tags: $headings
) {
	@each $heading, $fontsize in $headings-tags {
		#{$heading},
		.#{$heading} {
			font-family: $headings-font-family;
			font-weight: $headings-font-weight;
			line-height: $headings-line-height;
			@if $headings-color != '' {
				color: $headings-color;
			}
		}
	}
}

@mixin s-headings-styles-class-only(
	$headings-font-family: $headings-font-family,
	$headings-font-weight: $headings-font-weight,
	$headings-line-height: map.get($line-height, 'heading'),
	$headings-color: $headings-color,
	$headings-tags: $headings
) {
	@each $heading, $fontsize in $headings-tags {
		.#{$heading} {
			font-family: $headings-font-family;
			font-weight: $headings-font-weight;
			line-height: $headings-line-height;
			@if $headings-color != '' {
				color: $headings-color;
			}
		}
	}
}
