@use 'sass:map';
@use 'sass:math';
@use '../libs/gin-tonic/styles/theme' as *;
@forward '../libs/gin-tonic/styles/theme';
@use '../libs/gin-tonic/styles/settings' as *;
@forward '../libs/gin-tonic/styles/settings';

// COLORS
$color-theme: #e63329;
$color-gray: #353535;
$color-gray-light: #a0a0a0;
$color-bg: #eeeae8;

$error-color: $color-theme;
$success-color: #e2ff00;
$warning-color: #ff9900;
$color-primary: $color-theme;
$color-text: $color-gray;

$spacer: 10px;

$grid-gutter-width: 30px;

$link-decoration: none;
$link-hover-decoration: none;

$default-transition-duration: 350ms;
$default-transition-function: cubic-bezier(0.35, 0, 0.25, 1);
$transition: $default-transition-duration $default-transition-function;

$custom-themes: (
	base: (
		background: $color-bg,
		headings: $color-gray,
		headings-light: $color-white,
		text: $color-text,
		accent: pink,
		primary: $color-theme,
		button: (
			background: $color-primary,
			background-active: darken($color-primary, 20%),
			color: $color-white,
			border: $color-primary,
			border-hover: darken($color-primary, 20%),
		),
		input: (
			placeholder: $color-gray-light,
			color: $color-text,
			label: $color-text,
			background: $color-transparent,
			border: $color-gray,
		),
		link: (
			color: $color-text,
			hover: $color-theme,
		),
	),
	light: (
		background: $color-white,
		headings: $color-gray,
		headings-light: $color-white,
		text: $color-white,
		accent: pink,
		primary: $color-theme,
		button: (
			background: $color-primary,
			background-active: darken($color-primary, 20%),
			color: $color-white,
			border: $color-primary,
			border-hover: darken($color-primary, 20%),
		),
		input: (
			placeholder: $color-gray-light,
			color: $color-text,
			label: $color-text,
			background: $color-transparent,
			border: $color-gray,
		),
		link: (
			color: $color-text,
			hover: $color-theme,
		),
	),
);

$themes: map.deep-merge($themes, $custom-themes);

// TYPO
$font-weight: (
	light: 200,
	base: 400,
	semibold: 600,
	bold: 700,
);

$font-size: (
	base: 20px,
	xs: 14px,
	sm: 16px,
	md: 18px,
	lg: 24px,
	xl: 26px,
	xxl: 30px,
	xxxl: 42px,
);

$line-height: (
	base: 1.5,
	small: 1.2,
	large: 1.3,
	heading: 48px,
	heading-small: 30px,
);

$headings: (
	h1: map.get($font-size, xxxl),
	h2: map.get($font-size, xxxl),
	h3: map.get($font-size, lg),
	h4: map.get($font-size, base),
	h5: map.get($font-size, base),
	h6: map.get($font-size, base),
);

$headings-mobile: $headings;

$font-size-base: map.get($font-size, base);
$font-weight-base: map.get($font-weight, base);
$font-family-base: 'Corporate S', Arial, Verdana, sans-serif;
$font-family-secondary: 'AGaramond', Arial, Verdana, sans-serif;
$headings-font-family: 'Corporate S', Arial, Verdana, sans-serif;
$headings-font-weight: map.get($font-weight, semibold);

// GRID
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
);

// Breadcrumb
$breadcrumb-font-size: map.get($font-size, xs);
$breadcrumb-font-weight: map.get($font-weight, light);
$breadcrumb-line-height: 18px;

// INPUT
$input-height: 35px;
$input-border-radius: 0px;

// LINK
$link-font-weight: map.get($font-weight, semibold);
$link-line-height: 32px;
