@use 'mixins' as *;
@forward 'mixins';

.c-menu-multilevel {
	margin: 0;
	position: absolute;
	width: 100%;
	opacity: 0;
	pointer-events: none;
	backface-visibility: hidden;
	padding: 0;
	max-height: 100%;
	overflow-y: auto;
	@include c-menu-multilevel;
	@include c-menu-multilevel-transitions;

	&.is-visible {
		opacity: 1;
		pointer-events: auto;
	}

	@include c-menu-multilevel-animations;

	&__container {
		position: relative;

		ul {
			padding: 0;
			margin: 0;
			list-style: none;

			&:not(.c-menu-multilevel__subnavigation) {
				transform-style: preserve-3d;
			}
		}

		li {
			position: relative;

			a {
				display: block;
				position: relative;
			}

			.c-menu-multilevel__submenu {
				display: none;
			}
		}
	}

	@include c-menu-multilevel-container;
	@include c-menu-multilevel-navigation-icon;
	@include c-menu-multilevel-link;

	&__wrapper {
		width: 100%;
		z-index: 3000;
		position: relative;
	}

	@include c-menu-multilevel-submenu(0);

	&.c-menu-multilevel__subview {
		li {
			&,
			&.c-menu-multilevel__subview--open > a,
			&.c-menu-multilevel__subview > a {
				display: none;
			}

			&.c-menu-multilevel__subview,
			&.c-menu-multilevel__subview .c-menu-multilevel__submenu,
			&.c-menu-multilevel__subview--open,
			&.c-menu-multilevel__subview--open > .c-menu-multilevel__submenu,
			&.c-menu-multilevel__subview--open > .c-menu-multilevel__submenu > li {
				display: block;
			}
		}
	}
}
