@use 'sass:map';
@use '../../variables.scss' as *;
@use '../../../libs/gin-tonic/styles/link/mixins.scss' as *;

.c-link {
	display: flex;
	flex-direction: row;

	&--inverted {
		@include themify('link', $style-base: true) {
			@include c-link(
				$color: theme-color('hover', 'color'),
				$color-hover: theme-color('color')
			);
		}
	}

	&--secondary {
		&,
		&:active,
		&:focus,
		&:hover {
			@include themify() {
				@include c-link(
					$color: theme-color('text'),
					$color-hover: theme-color('text'),
					$text-decoration: underline !important,
					$text-decoration-hover: underline,
					$font-weight: $font-weight-base
				);
			}
			text-underline-offset: 15%;
			text-decoration-thickness: 1px !important;
		}
	}

	&__icon {
		width: $font-size-base;
		margin-left: map.get($spacers, 2);

		img {
			object-fit: contain;
		}
	}
}
