@use 'sass:math';
@use '../settings' as *;
@use '../breakpoints' as *;

// Framework grid generation

// Used only by Bootstrap to generate the correct number of grid classes given
// any value of `$grid-columns`.
@mixin o-grid-columns(
	$columns: $grid-columns,
	$gutter: $grid-gutter-width,
	$breakpoints: $grid-breakpoints
) {
	// Common properties for all breakpoints
	%grid-column {
		position: relative;
		width: 100%;
		min-height: 1px; // Prevent columns from collapsing when empty
		padding-right: ($gutter * 0.5);
		padding-left: ($gutter * 0.5);
	}

	@each $breakpoint in map-keys($breakpoints) {
		$infix: breakpoint-infix($breakpoint, $breakpoints);

		// Allow columns to stretch full width below their breakpoints
		@for $i from 1 through $columns {
			.col#{$infix}-#{$i} {
				@extend %grid-column;
			}
		}
		.col#{$infix},
		.col#{$infix}-auto {
			@extend %grid-column;
		}

		@include media-breakpoint-up($breakpoint, $breakpoints) {
			// Provide basic `.col-{bp}` classes for equal-width flexbox columns
			.col#{$infix} {
				flex-basis: 0;
				flex-grow: 1;
				max-width: 100%;
			}
			.col#{$infix}-auto {
				flex: 0 0 auto;
				width: auto;
				max-width: none; // Reset earlier grid tiers
			}

			@for $i from 1 through $columns {
				.col#{$infix}-#{$i} {
					@include o-col($i, $columns);
				}
			}

			.order#{$infix}-first {
				order: -1;
			}

			.order#{$infix}-last {
				order: $columns + 1;
			}

			@for $i from 0 through $columns {
				.order#{$infix}-#{$i} {
					order: $i;
				}
			}

			// `$columns - 1` because offsetting by the width of an entire row isn't possible
			@for $i from 0 through ($columns - 1) {
				@if not($infix == '' and $i == 0) {
					// Avoid emitting useless .offset-0
					.offset#{$infix}-#{$i} {
						@include o-col-offset($i, $columns);
					}
				}
			}
		}
	}
}

@mixin o-container($gutter: $grid-gutter-width) {
	width: 100%;
	padding-right: ($gutter * 0.5);
	padding-left: ($gutter * 0.5);
	margin-right: auto;
	margin-left: auto;
}

@mixin o-container-responsive(
	$breakpoints: $grid-breakpoints,
	$gutter: $grid-gutter-width,
	$max-widths: $container-max-widths
) {
	@each $breakpoint in map-keys($breakpoints) {
		$infix: breakpoint-infix($breakpoint, $breakpoints);
		$bigger-max-widths: max-widths-bigger($breakpoint, $max-widths);

		@if $infix != '' {
			.container#{$infix} {
				@include media-breakpoint-up($breakpoint) {
					@include o-container($gutter);
				}
				@if length($bigger-max-widths) >= 1 {
					@include o-container-max-widths($bigger-max-widths, $breakpoints);
				}
			}
		}
	}
}

@mixin o-container-fluid-responsive(
	$breakpoints: $grid-breakpoints,
	$gutter: $grid-gutter-width,
	$max-widths: $container-max-widths
) {
	@each $breakpoint in map-keys($breakpoints) {
		$infix: breakpoint-infix($breakpoint, $breakpoints);
		$bigger-max-widths: max-widths-bigger($breakpoint, $max-widths);

		@if $infix != '' {
			.container#{$infix}-fluid {
				@include o-container($gutter);
				@if length($bigger-max-widths) >= 1 {
					@include o-container-max-widths($bigger-max-widths, $breakpoints);
				}
			}
		}
	}
}

@mixin o-row-responsive(
	$breakpoints: $grid-breakpoints,
	$gutter: $grid-gutter-width
) {
	@each $breakpoint in map-keys($breakpoints) {
		$infix: breakpoint-infix($breakpoint, $breakpoints);

		@if $infix != '' {
			.row#{$infix} {
				@include o-gutters-not-important(0);

				@include media-breakpoint-up($breakpoint) {
					@include o-row($gutter);
					@include o-gutters-not-important($gutter * 0.5);
				}
			}
		}
	}
}

// For each breakpoint, define the maximum width of the container in a media query
@mixin o-container-max-widths(
	$max-widths: $container-max-widths,
	$breakpoints: $grid-breakpoints
) {
	@each $breakpoint, $container-max-width in $max-widths {
		@include media-breakpoint-up($breakpoint, $breakpoints) {
			max-width: $container-max-width;
		}
	}
}

@mixin o-row($gutter: $grid-gutter-width) {
	display: flex;
	flex-wrap: wrap;
	margin-right: math.div($gutter, -2);
	margin-left: math.div($gutter, -2);
}

@mixin o-col-ready($gutter: $grid-gutter-width) {
	position: relative;

	// Prevent columns from becoming too narrow when at smaller grid tiers by
	// always setting `width: 100%;`. This works because we use `flex` values
	// later on to override this initial width.
	width: 100%;
	min-height: 1px; // Prevent collapsing
	padding-right: ($gutter * 0.5);
	padding-left: ($gutter * 0.5);
}

@mixin o-col($size, $columns) {
	flex: 0 0 math.percentage(math.div($size, $columns));

	// Add a `max-width` to ensure content within each column does not blow out
	// the width of the column. Applies to IE10+ and Firefox. Chrome and Safari
	// do not appear to require this.
	max-width: math.percentage(math.div($size, $columns));
}

@mixin o-col-offset($size, $columns) {
	$num: math.div($size, $columns);

	margin-left: if($num == 0, 0, math.percentage($num));
}

@mixin o-gutters($width) {
	@if $width == 0 {
		margin-left: 0 !important;
		margin-right: 0 !important;
	} @else {
		margin-right: -$width !important;
		margin-left: -$width !important;
	}

	> .col,
	> [class*='col-'] {
		padding-right: $width !important;
		padding-left: $width !important;
	}
}

@mixin o-gutters-not-important($width) {
	@if $width == 0 {
		margin-left: 0;
		margin-right: 0;
	} @else {
		margin-right: -$width;
		margin-left: -$width;
	}

	> .col,
	> [class*='col-'] {
		padding-right: $width;
		padding-left: $width;
	}
}

@mixin o-no-gutters() {
	@include o-gutters(0);
}
