@mixin c-menu-multilevel-animations(
	$animation-duration: 150ms,
	$animation-function: cubic-bezier(0.25, 0.8, 0.25, 1)
) {
	@keyframes menu-multilevel-anim-out-2 {
		100% {
			transform: translateX(-100%);
			opacity: 0;
		}
	}
	@keyframes menu-multilevel-anim-in-2 {
		0% {
			transform: translateX(-100%);
			opacity: 0;
		}

		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
	@keyframes submenu-multilevel-anim-out-2 {
		0% {
			transform: translateX(0%);
			opacity: 1;
		}

		100% {
			transform: translateX(100%);
			opacity: 0;
		}
	}
	@keyframes submenu-multilevel-anim-in-2 {
		0% {
			transform: translateX(100%);
			opacity: 0;
		}

		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}

	&.is-animate-in {
		animation: menu-multilevel-anim-in-2 $animation-duration $animation-function;
	}

	&.is-animate-out {
		animation: menu-multilevel-anim-out-22 $animation-duration
			$animation-function;
	}

	&__wrapper {
		> .c-menu-multilevel__submenu,
		.c-menu-multilevel__container > .c-menu-multilevel__submenu {
			&.is-animate-in {
				animation: submenu-multilevel-anim-in-2 $animation-duration
					$animation-function;
			}

			&.is-animate-out {
				animation: submenu-multilevel-anim-out-22 $animation-duration
					$animation-function;
			}
		}
	}
}

@mixin c-menu-multilevel-submenu($top-position: 0) {
	&__wrapper {
		> .c-menu-multilevel__submenu,
		.c-menu-multilevel__container > .c-menu-multilevel__submenu {
			position: absolute;
			width: 100%;
			top: $top-position;
			left: 0;
			margin: 0;
		}
	}
}

@mixin c-menu-multilevel-container(
	$height: 100vh,
	$background-color: #000,
	$transition-duration: 150ms,
	$transition-function: cubic-bezier(0.25, 0.8, 0.25, 1)
) {
	&__container {
		background-color: transparent;
		height: 0;
		overflow: hidden;
		opacity: 0;
		transition: opacity $transition-duration $transition-function,
			background $transition-duration $transition-function;
	}

	&__wrapper {
		&.is-active {
			.c-menu-multilevel__container {
				opacity: 1;
				background-color: $background-color;
				height: $height;
			}
		}
	}
}

@mixin c-menu-multilevel(
	$max-height: calc(100% - 40px),
	$padding: 0 0 40px !important
) {
	max-height: $max-height;
	padding: $padding;
}

@mixin c-menu-multilevel-transitions(
	$transition-duration: 150ms,
	$transition-function: cubic-bezier(0.25, 0.8, 0.25, 1)
) {
	transition: transform $transition-duration $transition-function;

	&.is-toggle {
		transition: opacity $transition-duration $transition-function,
			background $transition-duration $transition-function,
			transform $transition-duration $transition-function height
			$transition-duration $transition-function;
	}
}

@mixin c-menu-multilevel-link(
	$padding: 30px 35px,
	$font-size: 18px,
	$line-height: normal,
	$color: #fff,
	$font-weight: 300
) {
	&__container {
		li {
			a {
				padding: $padding;
				font-size: $font-size;
				line-height: $line-height;
				font-weight: $font-weight;
				color: $color;
				outline: none;
			}
		}
	}
}
@mixin c-menu-multilevel-navigation-icon(
	$content: '\f105',
	$font-family: 'FontAwesome',
	$font-size: 20px,
	$color: #fff,
	$forward-right-position: 20px,
	$back-left-position: 20px,
	$back-padding-left: 40px
) {
	&__container {
		ul {
			&:not(.c-menu-multilevel__subnavigation) {
				li {
					&.c-menu-multilevel__back > a::after,
					> a:not(:only-child)::after {
						content: $content;
						position: absolute;
						top: 50%;
						height: $font-size;
						margin-top: $font-size * -0.5;
						font-size: $font-size;
						color: $color;
						font-family: $font-family;
					}

					> a {
						&::after {
							right: $forward-right-position;
						}
					}

					&.c-menu-multilevel__back {
						> a {
							padding-left: $back-padding-left;

							&::after {
								right: inherit;
								left: $back-left-position;
								transform: rotate(180deg);
							}
						}
					}
				}
			}
		}
	}
}
