@use 'sass:color';
@use '../hover/mixins' as *;
@use '../theme/mixins' as *;
@use '../settings' as *;

@mixin u-bg-variant($color) {
	background-color: $color;
}

@mixin u-bg-variant-hover($color, $color-hover) {
	@include u-bg-variant($color);
	@include m-hover-focus {
		background-color: $color-hover;
	}
}

@mixin u-bg-gradient-variant($parent, $color) {
	background: $color
		linear-gradient(180deg, color.mix($parent, $color, 15%), $color) repeat-x;
}

@mixin u-bg-img() {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

@mixin u-bg-variants($colors: $themes) {
	.u-bg {
		@include themify($style-base: true, $themes: $themes) {
			background-color: theme-color('background');
		}
	}
}
