@use '../backdrop' as *;

@mixin c-overlay(
	$background-color: rgba(black, 0.5),
	$z-index: 1000,
	$backdrop-animation-duration: 400ms,
	$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1)
) {
	position: absolute;

	&,
	&__backdrop,
	&__container {
		z-index: $z-index;
		pointer-events: none;
	}

	// TODO: or mixin c-overlay-backdrop?
	&__backdrop {
		pointer-events: auto;
		transition: opacity $backdrop-animation-duration
			$backdrop-animation-timing-function;
		opacity: 0;

		&.is-showing {
			opacity: 1;
		}

		// TODO: why c-backdrop?
		@include c-backdrop(absolute, $z-index);
	}

	&__container {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		&:empty {
			// Hide the element when it doesn't have any child nodes. This doesn't
			// include overlays that have been detached, rather than disposed.
			display: none;
		}
	}

	&__global {
		display: flex;
		justify-content: center;
		align-items: center;
		pointer-events: none;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: $z-index;
	}

	// add some themes
	&-dark-backdrop {
		@include c-backdrop-theme($background-color);
	}

	&-transparent-backdrop {
		// Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from
		// capturing the user's mouse scroll events. Since we also can't use something like
		// `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at
		// all and using `opacity` to make the element transparent.
		&,
		&.is-showing {
			opacity: 0;
		}
	}
}
