@use '../../variables.scss' as *;
@use '../../../libs/gin-tonic/styles/grid/mixins.scss' as *;

.c-navigation {
	width: 100%;

	&__container {
		@include o-container;
		@include o-container-max-widths;
		display: flex;
		justify-content: space-between;
	}

	&__heading {
		@include o-container;
		@include o-container-max-widths;
		padding-bottom: 10px;
	}

	&__toggler {
		position: fixed;
		z-index: 1000;
		top: 0;
		display: flex;
		height: 35px;
		padding: 8px;
		aspect-ratio: 1 / 1;
		border-radius: 100%;
		background-color: theme-color('primary');
		justify-content: center;
		margin-top: 40px;

		&__img {
			height: 100%;
		}
	}

	&__logo {
		display: block;
		height: 100px;
		margin-left: auto;

		&__img {
			height: 100%;
		}
	}
}