@use '../breakpoints' as *;
@use '../settings' as *;

@mixin u-spacing($grid-breakpoints: $grid-breakpoints, $spacers: $spacers) {
	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			$infix: breakpoint-infix($breakpoint, $grid-breakpoints);
			@include u-spacing-base($spacers, $infix);
		}
	}
}

@mixin u-spacing-base($spacers: $spacers, $infix: '') {
	@each $prop, $abbrev in (margin: m, padding: p) {
		@each $size, $length in $spacers {
			@if not($abbrev == p and $length < 0) {
				.#{$abbrev}#{$infix}-#{$size} {
					#{$prop}: $length !important;
				}

				.#{$abbrev}t#{$infix}-#{$size},
				.#{$abbrev}y#{$infix}-#{$size} {
					#{$prop}-top: $length !important;
				}

				.#{$abbrev}r#{$infix}-#{$size},
				.#{$abbrev}x#{$infix}-#{$size} {
					#{$prop}-right: $length !important;
				}

				.#{$abbrev}b#{$infix}-#{$size},
				.#{$abbrev}y#{$infix}-#{$size} {
					#{$prop}-bottom: $length !important;
				}

				.#{$abbrev}l#{$infix}-#{$size},
				.#{$abbrev}x#{$infix}-#{$size} {
					#{$prop}-left: $length !important;
				}
			}
		}
	}

	// Some special margin utils
	.m#{$infix}-auto {
		margin: auto !important;
	}
	.mt#{$infix}-auto,
	.my#{$infix}-auto {
		margin-top: auto !important;
	}
	.mr#{$infix}-auto,
	.mx#{$infix}-auto {
		margin-right: auto !important;
	}
	.mb#{$infix}-auto,
	.my#{$infix}-auto {
		margin-bottom: auto !important;
	}
	.ml#{$infix}-auto,
	.mx#{$infix}-auto {
		margin-left: auto !important;
	}
}
