@mixin c-backdrop-theme($background: rgba(0, 0, 0, 0.5)) {
	background: $background;
}

@mixin c-backdrop($position: absolute, $z-index: 0) {
	position: $position;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	@if $z-index > 0 {
		z-index: $z-index;
	}
}

@mixin c-backdrop-hidden() {
	opacity: 0;
	pointer-events: none;
}

@mixin c-backdrop-show() {
	opacity: 1;
	pointer-events: auto;
}
