@use '../theme' as *;
@use '../settings' as *;

@mixin c-input-field(
	$transition: get-existing-value($input-transition, $transition),
	$border-radius: get-existing-value($input-border-radius, $border-radius, 0)
) {
	border-radius: $border-radius;
	outline: none;
	width: 100%;
	box-shadow: none;
	box-sizing: border-box;
	transition: box-shadow $transition, border $transition, background $transition;
}

@mixin c-input-field-size(
	$font-size: get-existing-value($input-font-size, $font-size-base, 14px),
	$input-height: get-existing-value($input-height, 50px),
	$font-weight:
		get-existing-value($input-font-weight, $font-weight-base, normal),
	$margin: get-existing-value($input-margin, 0 0 5px 0),
	$padding: get-existing-value($input-padding, 0)
) {
	font-size: $font-size;
	font-weight: $font-weight;
	margin: $margin;
	padding: $padding;
	height: $input-height;

	@at-root {
		textarea#{&} {
			height: auto;
		}
	}
}

@mixin c-input-field-theme(
	$color: theme-color('color'),
	$border-color: theme-color('border'),
	$error-color: theme-color('color-error', 'color'),
	$success-color: theme-color('color-success', 'color'),
	$disabled-color: theme-color('color-disabled', 'color'),
	$focus-color: theme-color('color-focus', 'color')
) {
	color: $color;
	background-color: transparent;
	border: 0;
	border-bottom: 1px solid $border-color;

	&:disabled,
	&[readonly='readonly'] {
		color: $disabled-color;
		border-bottom: 1px solid $disabled-color;
	}

	&:focus:not([readonly]) {
		border-bottom: 1px solid $focus-color;
		box-shadow: 0 1px 0 0 $focus-color;
	}

	&.is-filled.valid,
	&:focus.valid {
		border-bottom: 1px solid $success-color;
		box-shadow: 0 1px 0 0 $success-color;
	}

	&.input-validation-error,
	&:focus.input-validation-error {
		border-bottom: 1px solid $error-color;
		box-shadow: 0 1px 0 0 $error-color;
	}
}

/* stylelint-disable selector-pseudo-element-no-unknown,selector-pseudo-class-no-unknown */
@mixin c-input-placeholder-theme($color: theme-color('placeholder', 'color')) {
	&::input-placeholder {
		/* Chrome/Opera/Safari */
		color: $color;
	}

	&::placeholder {
		/* Firefox 19+ */
		color: $color;
	}

	&:input-placeholder {
		/* IE 10+ */
		color: $color;
	}

	&:placeholder {
		/* Firefox 18- */
		opacity: 1; // https://css-tricks.com/almanac/selectors/p/placeholder/#firefox-placeholder-color
		color: $color;
	}
}
/* stylelint-enable selector-pseudo-element-no-unknown,selector-pseudo-class-no-unknown */
