@use 'sass:map';
@use '../../variables.scss' as *;
@use '../../../libs/gin-tonic/styles/grid/mixins.scss' as *;

.c-footer {
	padding: map.get($spacers, 6) 0;

	&__form-container {
		display: flex;
		flex-direction: row;
		gap: map.get($spacers, 4);
	}

	&__form {
		flex-grow: 2;
	}

	&__button-container {
		flex-grow: 1;
		align-self: end;
		margin-bottom: map.get($spacers, 3);
	}

	&__button {
		width: 35px;
		transform: rotate(-90deg);
		padding: 7px;
		aspect-ratio: 1;
	}

	&__social-media {
		display: flex;
		flex-direction: row;
		gap: map.get($spacers, 4);

		&__icon {
			height: 40px;

			img {
				object-fit: contain;
			}
		}
	}
}

@include themify() {
	.c-footer {
		background-color: theme-color('background');
	}
}
