@use 'mixins' as *;
@forward 'mixins';

.u-embed-responsive {
	@include u-embed-responsive;

	& &__item,
	iframe,
	embed,
	object,
	video {
		@include u-embed-responsive-item;
	}

	@include u-embed-responsive-ratio(1, 1);
	@include u-embed-responsive-ratio(2, 1);
	@include u-embed-responsive-ratio(4, 3);
	@include u-embed-responsive-ratio(16, 9);
	@include u-embed-responsive-ratio(21, 9);
}
