@use '../settings/' as *;
@use 'sass:map';

@function theme-color($names...) {
	$theme: $current-theme;
	@if($theme == null) {
		$theme: map.get($themes, base)
	}
	@each $name in $names {
		$val: map.get($theme, $name);
		@if ($val != null) {
			@return $val
		}
	}

	@return null;
}

@mixin themify(
	$component: null,
	$all: true,
	$themes: $themes,
	$style-base: false,
	$invert: false,
	$solid: false,
	$outline: false
) {
	$current-theme: map-get($themes, base) !global;

	@if ($component !=null) {
		$current-theme: map-get($current-theme, $component) !global;
	}

	@content;

	@if ($all) {
		@each $theme-name, $theme-map in $themes {
			$current-theme: $theme-map !global;

			@if ($component !=null) {
				$current-theme: map-get($theme-map, $component) !global;
			}

			@if ($current-theme !=null) {
				@if ($style-base) {
					&--#{$theme-name} {
						@content;
					}
				} @else {
					@if ($invert==false) {
						.theme--#{$theme-name} #{&} {
							@content;
						}
					}
				}

				@if ($invert) {
					$inverted-theme: map-get($theme-map, #{$component}-inverted);

					@if ($inverted-theme !=null) {
						$current-theme: $inverted-theme !global;
					}

					.theme--#{$theme-name} #{&},
					.theme.theme--#{$theme-name} &#{&}--#{$theme-name} {
						@content;
					}
				}

				@if ($solid) {
					$solid-theme: map-get($theme-map, #{$component}-solid);

					@if ($solid-theme !=null) {
						$current-theme: $solid-theme !global;
					}

					&--#{$theme-name}#{&}--solid {
						@content;
					}
				}

				@if ($outline) {
					$outline-theme: map-get($theme-map, #{$component}-outline);

					@if ($outline-theme !=null) {
						$current-theme: $outline-theme !global;
					}

					&--#{$theme-name}#{&}--outline {
						@content;
					}
				}
			}
		}
	}

	$current-theme: null !global;
}
