@use 'sass:math';
@use '../breakpoints' as *;

@mixin u-embed-responsive() {
	position: relative;
	display: block;
	width: 100%;
	padding: 0;
	overflow: hidden;

	&::before {
		display: block;
		content: '';
	}
}

@mixin u-embed-responsive-item() {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 0;
}

@mixin u-embed-responsive-breakpoints-ratio($breakpoints, $value1, $value2) {
	@each $breakpoint in map-keys($breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			$infix: breakpoint-infix($breakpoint, $breakpoints);
			&--ratio#{$infix}-#{$value1}by#{$value2} {
				@include u-embed-responsive-ratio-before($value1, $value2);
			}
		}
	}
}
@mixin u-embed-responsive-ratio($value1, $value2) {
	&--ratio-#{$value1}by#{$value2} {
		@include u-embed-responsive-ratio-before($value1, $value2);
	}
}
@mixin u-embed-responsive-ratio-before($value1, $value2) {
	&::before {
		padding-top: math.percentage(math.div($value2, $value1));
	}
}
