@use 'sass:map';
@use 'mixins' as *;
@forward 'mixins';

// COLORS
$color-primary: #ff2d43 !default;
$color-accent: #ffbe1a !default;

$color-white: #fff !default;
$color-black: #000 !default;
$color-gray: #555 !default;
$color-transparent: #0000 !default;

$text-color: $color-black  !default;
$danger-color: $color-primary  !default;

// THEMES
$themes: (
	base: (
		background: $color-white,
		title: $color-black,
		text: $color-black,
		headings: $color-black,
		default: $color-primary,
		accent: $color-accent,
		button: (
			background: $color-primary,
			background-active: darken($color-primary, 20%),
			color: $color-white,
			border: $color-primary,
			border-hover: darken($color-primary, 20%),
		),
		input: (
			placeholder: $color-gray,
			color: $color-black,
			label: $color-black,
			background: $color-transparent,
			border: $color-black,
		),
		link: (
			color: $color-primary,
			hover: darken($color-primary, 20%),
			decoration: none,
		),
	),
	dark: (
		background: $color-black,
		title: $color-white,
		text: $color-white,
		headings: $color-white,
		default: $color-primary,
		accent: $color-accent,
		input-bg: $color-black,
		button: (
			background: $color-accent,
			background-active: darken($color-accent, 20%),
			color: $color-white,
			border: $color-accent,
			border-hover: darken($color-accent, 20%),
		),
		input: (
			placeholder: $color-gray,
			color: $color-white,
			label: $color-white,
			background: $color-transparent,
			border: $color-white,
		),
		link: (
			color: $color-primary,
			hover: darken($color-primary, 20%),
			decoration: none,
		),
	),
	) !default;


// SPACERS
$spacer: 10px !default;
$spacers: (
	-3: - ($spacer * 0.25),
	-2: - ($spacer * 0.5),
	-1: -1px,
	0: 0,
	1: ($spacer * 0.25),
	2: ($spacer * 0.5),
	3: $spacer,
	4: ($spacer * 1.5),
	5: ($spacer * 2),
	6: ($spacer * 3),
	7: ($spacer * 4),
	8: ($spacer * 6),
	9: ($spacer * 8),
	10: ($spacer * 5),
	11: ($spacer * 2.5),
	12: ($spacer * 15),
	13: ($spacer * 10),
	14: ($spacer * 7),
	15: ($spacer * 4.5),
	16: 1px,
	) !default;


// TYPO
$font-size: (
	base: 1rem,
	) !default;

$font-weight: (
	light: 300,
	normal: 400,
	medium: 500,
	bold: 600,
	extra-bold: 800,
	) !default;

$line-height: (
	base: 1,
	heading: 1.3,
	) !default;

$headings: (
	h1: 2.5rem,
	h2: 2rem,
	h3: 1.75rem,
	h4: 1.5rem,
	h5: 1.25rem,
	h6: 1rem,
	) !default;

$headings-mobile: (
	h1: 2rem,
	h2: 1.75rem,
	h3: 1.5rem,
	h4: 1.25rem,
	h5: 1rem,
	h6: 1rem,
	) !default;

$font-family-base: Arial !default;
$font-size-base: map-get($font-size, base) !default;
$font-weight-base: map-get($font-weight, normal) !default;
$line-height-base: map-get($line-height, base) !default;

$headings-font-family: $font-family-base  !default;
$headings-font-weight: map.get($font-weight, 'bold') !default;
$headings-desktop-breakpoint: md !default;

// GRID
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	) !default;

$container-max-widths: (
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1140px,
	) !default;

$grid-columns: 12 !default;
$grid-gutter-width: 20px !default;

// TRANSITION
$default-transition-duration: 350ms !default;
$default-transition-function: ease-in-out !default;
$transition: $default-transition-duration $default-transition-function  !default;

// BORDER
$border-width: 1px !default;
$border-color: $color-black  !default;
$border-style: solid !default;
$border: $border-width $border-style $border-color  !default;
$border-radius: 7px !default;

// BUTTON
$button-spacing-small-vertical: 6px !default;
$button-spacing-small-horizontal: 15px !default;
$button-spacing-vertical: 10px !default;
$button-spacing-horizontal: 15px !default;
$button-border: 1px !default;
$button-border-radius: $border-radius  !default;

// Breadcrumb
$breadcrumb-font-size: null !default;
$breadcrumb-font-weight: null !default;
$breadcrumb-line-height: null !default;

// LINK
$link-font-size: null !default;
$link-font-weight: null !default;
$link-line-height: null !default;
$link-color: $color-accent  !default;
$link-decoration: none !default;
$link-hover-color: $color-gray  !default;
$link-hover-decoration: none !default;

// INPUT-FIELD
$input-height: null !default;
$input-textarea-height: null !default;
$input-font-size: null !default;
$input-font-weight: null !default;

$input-margin: null !default;
$input-padding: null !default;

$input-border-width: null !default;
$input-border-radius: null !default;
$input-border: null !default;

$input-transition: null !default;

// FIELDS
$radio-border-width: 1px !default;
$radio-border: $radio-border-width solid $color-accent  !default;

$checkbox-border-radius: 0 !default;
$checkbox-icon-color-checked: $text-color  !default;
$checkbox-background-checked: $color-gray  !default;

$active-class: 'is-active' !default;

// BOX-SHADOW
$box-shadow-spread: 0 !default;
$box-shadow-spread-small: 0 !default;
$box-shadow-blur-small: 15px !default;
$box-shadow-blur: 17px !default;
$box-shadow-blur-hover: 30px !default;
$box-shadow-color: rgba(85, 85, 85, 0.68) !default;
$box-shadow-base: 0 0 $box-shadow-blur $box-shadow-spread  !default;
$box-shadow-base-hover: 0 0 $box-shadow-blur-hover $box-shadow-spread  !default;
$box-shadow: $box-shadow-base $box-shadow-color  !default;
$box-shadow-hover: $box-shadow-base-hover $box-shadow-color  !default;

$box-shadow-inset: inset 0 $box-shadow-blur $box-shadow-blur (
	$box-shadow-blur * -1 + $box-shadow-spread) $box-shadow-color  !default;