// stylelint-disable declaration-no-important

// Common values

// Sass list not in variables since it's not intended for customization.
@mixin u-positions($positions) {
	@each $position in $positions {
		.position-#{$position} {
			position: $position !important;
		}
	}
}

// Shorthand
@mixin u-fixed-top($offset: 0, $zindex: 99) {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: $zindex;
}

@mixin u-fixed-bottom($offset: 0, $zindex: 99) {
	position: fixed;
	right: 0;
	bottom: $offset;
	left: 0;
	z-index: $zindex;
}

@mixin u-sticky-top($offset: 0, $zindex: 99) {
	position: sticky;
	top: $offset;
	z-index: $zindex;
}
